html {
  scroll-behavior: smooth;
}

body {
  font-family: $opensans;
  margin: 0;
}

.nav-opened {
  overflow: hidden;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
